.mst-first-post-macarons-bg {
    background-image: url(../img/img-blog/blog-site-web.jpg);
    background-attachment: fixed;
    background-position: 50%;
    background-size: cover;
    height: 70rem;
    position: relative;
    width: 100%;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background-color: rgba(0, 0, 0, 0.5); /* Cambia el valor para ajustar la opacidad */
        }

            .mst-first-post-title-content {
                text-align: left;
                position: relative;
                top: 20rem;
                padding: 4rem;

                
                        h1 {
                            color: $white;
                            text-align: center;
                            font-weight: 200;
                            margin-top: 0;
                            font-size: 4.4375rem;
                            
                            @include tablet {
                                font-size: 6rem;
                            }
                        }

                        p {
                            color: $white;
                            font-weight: 200;
                            position: relative;
                            bottom: 2.5rem;
                            text-align: center;
                        }
                
                @include tablet {
                    position: relative;
                    top: 27rem;
                    color: $white;
                    text-align: center;
                    padding: 1rem 0;


                        h1 {
                            font-weight: 200;
                            font-size: 6rem;
                            text-align: center;
                            margin: 0;
                        }

                        p {
                            color: $white; // #FF5733
                            font-size: 1.5rem;
                            font-weight: 200;
                            text-align: center;
                            position: relative;
                            bottom: 1rem;
                        }
                }


                .mst-btn-first-post-separation {

                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin: 3rem 0;

    
                       .mst-btns {
                        font-family: 'Newton';
                        line-height: 1.3125rem;
                        font-size: 1.7rem;
                        background-color: var(--mst-button-background-pink); //#424953
                        width: 200px;
                        padding: 15px 20px;
                        text-align: center;
                        color: #F4F5F0;
                        border-radius: 10px;
                        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
                        transition: 0.1s;
    
    
                            &:hover, &:focus, &:active {
                                box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
                                transform: scale(1.1);
                            }
                    }
             }
            }
}


.mst-section-first-post-content {

    background-color: #f5f5f5; //#424953
    padding: 3rem;

    @include tablet {
        padding: 5rem;
    }


    @include desktop {
        padding: 0 5rem 0 0;
    }


.content-first-post-tittle-mst-section {
    

    @include desktop {
        display: grid;
        grid-template-columns: 1fr 2fr;
        gap: 8rem;
    }

    .mist-bg-pt-lg-img {
        display: none;

        @include desktop {
        background-attachment: fixed;
        background-image: url(../img/img-posts/coffe-laptop.webp);
        background-repeat: no-repeat;
        background-size: contain;
        display: block;
        height: 100%;
        position: relative;
        width: 100%;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-color: rgba(0, 0, 0, 0.5); /* Cambia el valor para ajustar la opacidad */
            }

        }

    }

    @include desktop {
        // padding: 7rem 5rem 7rem 0;
    }
                h2 {
                    font-size: 2.5rem;
                    margin: 0;
                }   


                h1 {
                    text-align: left; //center
                    font-size: 4.9rem;

                    @include tablet {
                        text-align: left;
                        margin: 3.5rem 0;
                        font-size: 5.5rem;

                    }

                    @include tablet {
                        font-size: 5.5rem;
                    }
                    
                 }

                p {
                    text-align: left; //center
                    font-size: 1.7rem;
                    margin: 3rem 0;
                    padding: 0;

                    @include tablet {
                        // padding: 0 4rem;
                    }

                    @include desktop {
                        padding: 0;
                    }

                    @include desktop {
                        text-align: left;
                        margin: 1rem 0;
                        padding-left: 0;
                        padding-bottom: 3rem;
                    }
                    
                }

                .mst-first-post-text-complete {
                    
                    display: flex;
                    flex-direction: column;
                    border-radius: 10px;
                    margin-bottom: 5rem;

                    .mst-paragraph-tittle-first-post {
                        p {
                            text-align: center;
                        }
                    }
            
                    @include tablet {
                        padding: 2rem .5rem;
            
                    }
                    
                    @include desktop {
                        padding: 5rem 0;
                        
                    }

                    h2 {
                        font-size: 3rem;
                        margin: 0;
                        margin-bottom: 2.5rem;
                    }   
            
                }

                .mst-btn-position-first-post {

                        display: flex;
                        justify-content: center;
                        align-items: center;

                        @include desktop {
                            display: flex;
                            justify-content: flex-start;
                            align-items: center;
                        }

                        .mst-btns {
                            font-family: 'Newton';
                            line-height: 1.3125rem;
                            font-size: 1.7rem;
                            background-color: var(--mst-button-background-blue); //#424953
                            width: 200px;
                            padding: 15px 20px;
                            text-align: center;
                            color: #F4F5F0;
                            border-radius: 10px;
                            box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
                            transition: 0.1s;
        
        
                                &:hover, &:focus, &:active {
                                    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
                                    transform: scale(1.1);
                                }
                        }
                }

        }
}